@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
* {
  font-family: 'Roboto', sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  letter-spacing: .5px;
}

body{color:#334666}body::-webkit-scrollbar{width:12px;height:12px}body::-webkit-scrollbar-button{width:0px;height:0px}body::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}body::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}.home{display:grid;grid-gap:2rem;gap:2rem;background:#f9fafb;min-height:100vh;width:100%;padding:.5rem}.isLoading{opacity:.5;filter:grayscale(0.75)}@media screen and (min-width: 576px){.home{padding:1rem}}@media screen and (min-width: 768px){.home{padding:2rem;grid-template-rows:auto auto auto auto 1fr}}@media screen and (min-width: 1100px){.home{grid-template-columns:repeat(12, 1fr)}.chart{grid-column:1 / 7}.vaccination{grid-column:7 / 13}}

.basic-data-display{display:flex;flex-wrap:wrap;grid-gap:25px;gap:25px}.basic-data-display__header{font-size:1.75rem;flex:0 0 100%;padding:0 1rem}.basic-data-display__data{flex:1 1 100%;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;background:#fff;padding:1rem;display:flex;justify-content:space-between;align-items:center;border-radius:1rem}.basic-data-display__data-left{display:flex;flex-direction:column;grid-gap:.25rem;gap:.25rem}.basic-data-display__data-name{text-transform:capitalize;font-weight:700}.basic-data-display__data-value{font-weight:700;font-size:1.25rem}.basic-data-display__data-new-value{font-size:.9rem}.basic-data-display__data-new-value.plus{color:#82d416}.basic-data-display__data-new-value.plus::before{content:' '}.basic-data-display__data-new-value.minus{color:#ea0606}.basic-data-display__data-new-value.minus::before{content:' '}.basic-data-display__data-icon-box{background:linear-gradient(90deg, #21d4fd 0%, #2152ff 100%);color:#fff;width:50px;height:50px;display:flex;align-items:center;justify-content:center;border-radius:.5rem}.basic-data-display__data-icon{font-size:1.25rem}@media screen and (min-width: 576px){.basic-data-display{display:grid;grid-template-columns:1fr 1fr}.basic-data-display__header{font-size:2rem;grid-column:1 / 3}.basic-data-display__data-value{font-size:1.5rem}}@media screen and (min-width: 1100px){.basic-data-display{grid-column:1 / 7;grid-row:2}}

.table-row{display:flex;align-items:center;width:100%}.table-row:nth-child(even){background:#f9fafb}.table-row__td{flex:1 0 150px;padding:.5rem}.table-row__td:first-child{flex:0 0 auto}.table-row__td-wrapper,.table-row__td-wrapper--first{display:flex;flex-direction:column;align-items:center;grid-gap:.25rem;gap:.25rem}.table-row__td-wrapper--first{align-items:flex-start}.table-row__td-name,.table-row__td-name--first{font-size:.9rem}.table-row__td-value,.table-row__td-value--first{font-weight:700}

.sort-by{font-weight:400}.sort-by__list{display:flex;align-items:center;white-space:nowrap;grid-gap:.5rem;gap:.5rem;padding:.5rem 0 1rem 0;list-style-type:none;overflow:auto}.sort-by__text{font-weight:700}@media screen and (min-width: 768px){.sort-by::-webkit-scrollbar{width:12px;height:12px}.sort-by::-webkit-scrollbar-button{width:0px;height:0px}.sort-by::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}.sort-by::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}}

.sort-option{background:rgba(77,77,77,0.1);border:1px solid rgba(51,51,51,0.1);border-radius:1rem;color:#333;cursor:pointer;transition:.2s ease-in-out;opacity:.5;padding:.5rem}.sort-option:hover{opacity:.75}.sort-option.active{opacity:1}.sort-option.asc:after,.sort-option.desc:after{display:inline-block;font-style:normal;font-feature-settings:normal;font-variant:normal;text-rendering:auto;-webkit-font-smoothing:antialiased;font-family:"Font Awesome 5 Free";font-weight:900;margin-left:.5rem}.sort-option.active.desc:after{content:"\f0d7"}.sort-option.active.asc:after{content:"\f0d8"}

.countries-table{display:block;position:relative;width:100%;background:#fff;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;border-radius:1rem;border-collapse:collapse;overflow:hidden;padding:2rem 1rem .5rem 1rem}.countries-table__thead{display:block;width:100%}.countries-table__tr{display:flex;flex-direction:column;grid-gap:.5rem;gap:.5rem;width:100%}.countries-table__title{font-size:1.25rem;text-align:left;display:inline-block;text-transform:capitalize}.countries-table__tbody{display:block;overflow:auto;width:100%;height:250px;margin-top:.5rem}@media screen and (min-width: 768px){.countries-table__title{font-size:1.5rem}.countries-table__tbody{height:300px}.countries-table__tbody::-webkit-scrollbar{width:12px;height:12px}.countries-table__tbody::-webkit-scrollbar-button{width:0px;height:0px}.countries-table__tbody::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}.countries-table__tbody::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}}@media screen and (min-width: 1100px){.countries-table{grid-column:1 / 7}}

.header{position:-webkit-sticky;position:sticky;top:.5rem;z-index:5;padding:1rem .5rem;margin:.25rem 0 0 0;background:#fff;border-radius:1rem;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;display:flex;align-items:center;justify-content:space-between;grid-gap:.25rem;gap:.25rem}.header__logo{display:flex;align-items:center;grid-gap:.5rem;gap:.5rem;color:inherit;text-decoration:none}.header__text{font-size:1.25rem;display:none}.header__icon{font-size:2.5rem}.header__options{display:flex;grid-gap:.5rem;gap:.5rem}@media screen and (min-width: 576px){.header__text{font-size:1.5rem;display:block}}@media screen and (min-width: 1100px){.header{grid-column:1 / 13}}

.modal{background:rgba(0,0,0,0.5);width:100vw;height:100vh;display:flex;justify-content:center;align-items:center;position:fixed;top:0;left:0;z-index:100}

.header-option{display:flex;grid-gap:.5rem;gap:.5rem;align-items:center;justify-content:center;background:linear-gradient(310deg, #141727, #3a416f);box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;border-radius:1rem;color:#fff;padding:.75rem;font-size:.9rem;cursor:pointer}@media screen and (min-width: 576px){.header-option{font-size:1rem}}

.date-change{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;min-width:250px;background:#fff;padding:1rem;border-radius:1rem;box-shadow:rgba(50,50,93,0.25) 0px 13px 27px -5px,rgba(0,0,0,0.3) 0px 8px 16px -8px}.date-change__header{display:flex;justify-content:space-between}.date-change__title{font-size:1.5rem}.date-change__close{font-size:1.5rem;cursor:pointer;transition:.1s ease-in-out}.date-change__close:hover{color:rgba(51,70,102,0.75)}.date-change__inputs-group{display:flex;grid-gap:.5rem;gap:.5rem}.date-change__input{width:80px;padding:.5rem;font-size:1rem;outline:none}.date-change__buttons-group{display:flex;justify-content:space-between}.date-change__button{padding:.5rem;border:none;background:inherit;color:inherit;font-size:1rem;cursor:pointer;transition:.2s ease-in-out;font-weight:700}.date-change__button:hover{color:rgba(51,70,102,0.75)}

.country-change{display:flex;flex-direction:column;grid-gap:1rem;gap:1rem;background:#fff;padding:1rem;border-radius:1rem;min-width:250px;height:80vh;max-height:600px;box-shadow:rgba(50,50,93,0.25) 0px 13px 27px -5px,rgba(0,0,0,0.3) 0px 8px 16px -8px}.country-change__header{display:flex;justify-content:space-between}.country-change__title{font-size:1.5rem}.country-change__close{font-size:1.5rem;cursor:pointer;transition:.1s ease-in-out}.country-change__close:hover{color:rgba(51,70,102,0.75)}.country-change__countries-list{display:flex;flex-direction:column;list-style:none;overflow-y:auto;max-height:100%}.country-change__countries-list::-webkit-scrollbar{width:12px;height:12px}.country-change__countries-list::-webkit-scrollbar-button{width:0px;height:0px}.country-change__countries-list::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}.country-change__countries-list::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}.country-change__countries-list-item{display:flex;align-items:center;padding:.5rem;grid-gap:.5rem;gap:.5rem;cursor:pointer;max-width:200px}.country-change__countries-list-item:hover{color:rgba(51,70,102,0.75)}.country-change__country-flag{font-size:1.25rem;text-align:center;width:24px}

.search-box{border:1px solid #334666;border-radius:8px;display:flex;align-items:center;justify-content:space-between}.search-box__input{border-radius:inherit;padding:.5rem;margin:0 .5rem;font-size:1rem;border:none;outline:none}.search-box__icon{margin-right:1rem}

.map{padding:1rem;background:white;border-radius:1rem;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;overflow-x:auto}.map__geochart{height:100%}@media screen and (min-width: 768px){.map::-webkit-scrollbar{width:12px;height:12px}.map::-webkit-scrollbar-button{width:0px;height:0px}.map::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}.map::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}.map div{height:100%}.map__geochart{width:100%;height:100%;overflow:hidden}}@media screen and (min-width: 1100px){.map{grid-column:7 / 13;grid-row:2 / 4}}

.chart{background:#fff;padding:2rem 1rem 1rem 1rem;border-radius:1rem;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;overflow:auto}.chart__title{margin-bottom:1rem}.chart__body{height:300px;min-width:500px}@media screen and (min-width: 768px){.chart::-webkit-scrollbar{width:12px;height:12px}.chart::-webkit-scrollbar-button{width:0px;height:0px}.chart::-webkit-scrollbar-thumb{background:linear-gradient(310deg, #141727, #3a416f);border:none;border-radius:1rem}.chart::-webkit-scrollbar-track{border-radius:1rem;background:rgba(77,77,77,0.2)}}

.chart-options{display:flex;grid-gap:1rem;gap:1rem;padding:0 0 1rem 0;white-space:nowrap;margin-bottom:1rem}

.chart-select{background:rgba(77,77,77,0.1);border:1px solid rgba(51,51,51,0.1);border-radius:1rem;color:#333;min-width:105px;text-align:center;position:relative;font-size:.9rem;padding:.5rem 1rem;border-radius:1rem;cursor:pointer;transition:.1s ease-in-out}.chart-select:hover{box-shadow:rgba(0,0,0,0.1) 0px 20px 25px -5px,rgba(0,0,0,0.04) 0px 10px 10px -5px}.chart-select::after{display:inline-block;font-style:normal;font-feature-settings:normal;font-variant:normal;text-rendering:auto;-webkit-font-smoothing:antialiased;font-family:"Font Awesome 5 Free";font-weight:900;content:"\f107";margin-left:.5rem}.chart-select__dropdown{display:block;position:absolute;left:-2px;top:-2px;min-width:200px;z-index:2;text-align:left;color:#333;background:#fff;box-shadow:rgba(0,0,0,0.35) 0px 5px 15px;border-radius:8px;cursor:auto}.chart-select__option{list-style:none;padding:1rem;white-space:nowrap;cursor:pointer}.chart-select__option:first-child{border-radius:8px 8px 0 0}.chart-select__option:last-child{border-radius:0 0 8px 8px}.chart-select__option:hover{background:rgba(77,77,77,0.1)}

.chart-button{background:rgba(77,77,77,0.1);border:1px solid rgba(51,51,51,0.1);border-radius:1rem;color:#333;display:flex;align-items:center;font-size:.9rem;padding:.5rem 1rem;opacity:.5;cursor:pointer;transition:.2s ease-in-out}.chart-button:hover:not(.active){box-shadow:rgba(0,0,0,0.1) 0px 20px 25px -5px,rgba(0,0,0,0.04) 0px 10px 10px -5px;opacity:.75}.chart-button.active{opacity:1;cursor:auto}

.vaccination{background:#fff;border-radius:1rem;box-shadow:rgba(149,157,165,0.2) 0px 8px 24px;padding:2rem 1rem}.vaccination__date{margin-top:.5rem;font-size:.95rem}

.progress{padding:2rem 0}.progress__header{display:flex;justify-content:space-between;margin-bottom:1rem}.progress__info-list{margin-top:1rem;list-style:none;display:flex;flex-wrap:wrap;align-items:center;grid-gap:1rem;gap:1rem;width:100%}.progress__info-item{flex:1 0 100px;display:flex;grid-gap:.5rem;gap:.5rem;font-size:.9rem}.progress__info-item-right{display:flex;grid-gap:.25rem;gap:.25rem;flex-direction:column}.progress__info-text{white-space:nowrap}.progress__info-value{font-weight:700}

.progress-bar{position:relative;width:100%;height:1.5rem;border-radius:1rem;background:#ededed;transition:0.5s ease-in-out}.progress-bar__main,.progress-bar__secondary{position:absolute;height:inherit;border-radius:inherit;transition:inherit}.progress-bar__main{z-index:3}.progress-bar__secondary{z-index:2}

.tooltip{background:#333;padding:0.5rem;font-size:0.75rem;position:fixed;z-index:5;color:#fff;border-radius:1rem}

.footer{display:flex;justify-content:center;align-items:center;flex-wrap:wrap;grid-gap:.5rem;gap:.5rem;padding:0 1rem 1.5rem 1rem}.footer__copyright{font-size:.9rem}.footer__socials-list{list-style:none;display:flex;align-items:center;grid-gap:.75rem;gap:.75rem}.footer__social-icon{font-size:2rem;cursor:pointer;transition:.1s ease-in-out}.footer__social-icon:hover{opacity:.9}@media screen and (min-width: 576px){.footer{padding:.5rem 1rem 0 1rem}}@media screen and (min-width: 1100px){.footer{grid-column:1 / 13}}

.loading-screen{width:100vw;height:100vh;background:#f9fafb;position:absolute;left:0;top:0;z-index:1000}.loading-screen__spin{position:absolute;left:50%;top:50%;border:solid 3px #333;border-bottom-color:#f9fafb;border-radius:50%;height:75px;width:75px;animation:1s linear infinite spinner}@keyframes spinner{from{transform:translate3d(-50%, -50%, 0) rotate(0deg)}to{transform:translate3d(-50%, -50%, 0) rotate(360deg)}}

